<template>
  <div>
    <fTitle>
      <template #title>Steps 步骤条</template>
      <template #introduce>引导用户按照流程完成任务的分步导航条，可根据实际应用场景设定步骤，步骤不得少于 2 步。</template>
    </fTitle>
    <sTitle>
      <template #title>基础用法</template>
      <template #introduce>简单的步骤条。</template>
    </sTitle>
    <div class="outbox">
    <div class="inbox">
      <el-row type="flex" justify="space-around">
    <el-col :span="20">
<div class="grid-content">
  <el-tabs v-model="activeName">
    <el-tab-pane label="用户管理" name="first">
      <el-row type="flex" justify="space-around">
    <el-col :span="20">
<div class="grid-content">
  <el-slider
      @change="test1"
      v-model="value2"
      :step="10"
      show-stops>
    </el-slider>
</div>
    </el-col></el-row>

    </el-tab-pane>
    <el-tab-pane label="配置管理" name="second">
      <el-row type="flex" justify="space-around">
    <el-col :span="6">
<div ref="box1" class="grid-col" @click="randomColor"></div>
    </el-col>
    <el-col :span="6">
<div ref="box2" class="grid-col" @click="randomColor"></div>
    </el-col>
    <el-col :span="6" >
<div ref="box3" class="grid-col" @click="randomColor"></div>
    </el-col>
  </el-row>
    </el-tab-pane>
    <el-tab-pane label="角色管理" name="third">
      <el-row type="flex" justify="space-around">
    <el-col :span="20">
<div class="grid-content">
  <span>{{ item1 }}</span>+<span>{{ item2 }}</span>=
  <el-input
  style="display: inline;"
  v-model="result"
  placeholder="请输入结果"
  @blur="check"
  @keyup.enter="check"></el-input>
</div>
    </el-col></el-row>
    </el-tab-pane>
  </el-tabs>
</div>
    </el-col></el-row>
    <el-row type="flex" justify="space-around">
    <el-col :span="20">
<div class="grid-content">
  <el-steps :active="num" align-center finish-status="success">
  <el-step title="步骤 1" description="将拉条拉直100"></el-step>
  <el-step title="步骤 2" description="将所有黑色box变为虚线"></el-step>
  <el-step title="步骤 3" description="计算出正确答案"></el-step>
</el-steps>
</div>
    </el-col></el-row>
    </div></div>
  </div>

</template>

<script>
export default {
  name: 'ColorIndex',
  data () {
    return {
      activeName: 'first',
      value2: 0,
      classBox: ['pink', 'ok', 'blue'],
      result: '',
      num: 0
    }
  },
  methods: {
    test1 () {
      if (this.value2 === 100) {
        this.num++
      }
    },
    randomColor (e) {
      // vue中@click后面的函数不要跟括号
      const index = Math.floor(Math.random() * this.classBox.length)
      e.target.classList = this.classBox[index]
      console.log(e.target.classList)
      if (this.$refs.box1.classList.contains('ok') &&
      this.$refs.box2.classList.contains('ok') &&
      this.$refs.box3.classList.contains('ok') &&
      this.num === 1) {
        this.num++
      }
    },
    check () {
      if (+this.result === this.realResult) {
        this.$message({
          message: '恭喜你答对了',
          type: 'success'
        })
        if (this.num === 2) {
          this.num++
        }
        return 0
      }
      if (this.result === '') {
        this.$message({
          message: '请输入非空答案',
          type: 'warning'
        })
      } else {
        this.$message({
          message: '回答错误，请重新尝试',
          type: 'warning'
        })
        console.log(this.realResult)
      }
    }
  },
  computed: {
    item1 () {
      return +Math.floor(Math.random() * 100)
    },
    item2 () {
      return +Math.floor(Math.random() * 100)
    },
    realResult () {
      return this.item1 + this.item2
    }
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.el-input--small /deep/ .el-input__inner {
    height: 32px;
    width: 100px;}
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .grid-content{
        height: auto;
        background: white;
        margin-bottom: 20px;
        align-items: center;}
    }
    .grid-col{
      cursor: pointer;
      width: 100px;
      height: 100px;
      border-radius: 5px;
      background-color: black;

    }
    .pink{
      cursor: pointer;
      width: 100px;
      height: 100px;
      border-radius: 5px;
      background-color: pink;
    }
    .blue{
      cursor: pointer;
      width: 100px;
      height: 100px;
      border-radius: 5px;
      background-color:rgb(128, 176, 249)
    }
    .ok{
      cursor: pointer;
      width: 100px;
      height: 100px;
      border-radius: 5px;
      background-color:white;
      border: 2px dashed rgb(193, 252, 104);
    }
    }
</style>
